@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;600;700&display=swap);
:root {
  --primary-clr: hsl(0, 69%, 50%);
  --primary-soft-clr: hsl(0, 69%, 95%);
  --primary-darken-clr: hsl(0, 69%, 45%);
  --secondary-clr: hsl(31, 100%, 48%);
  --secondary-soft-clr: hsl(31, 100%, 93%);
  --secondary-darken-clr: hsl(31, 100%, 43%);
  --dark-clr: hsl(201, 100%, 14%);
  --light-clr: hsl(0, 0%, 85%);
}
*,
*::before,
*:after {
  outline: none;
  box-sizing: border-box;
}
h1,h2,h3,p{color: hsl(201, 100%, 14%);color: var(--dark-clr)}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Rubik', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#container {
  width: 75%;
  max-width: 1178px;
  margin: 0 auto
}
@media only screen and (max-width: 850px) {
  #container {width: 85% !important}
  html{font-size: 14px}
}
header,
#container.header,
.navbar,
.navbar ul {display: -webkit-flex;display: flex}
header {
    width: 100%;
    height: 72px;
    background-color: #fff;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 9999
}
#container.header {
    -webkit-align-items: center;
            align-items: center;
    -webkit-align-self: center;
            align-self: center;
    -webkit-flex-basis: 76%;
            flex-basis: 76%;
    -webkit-justify-content: space-between;
            justify-content: space-between
}
.box_shadow {box-shadow: 0px -25px 32px black}
.logo img {
    width: 42px;
    cursor: pointer;
    transition: .1s ease-out
}
.logo img:hover {
    opacity: .8;
    transition: .1s ease-out
}
.navbar {-webkit-align-items: center;align-items: center}
.navbar ul {
    -webkit-justify-content: space-between;
            justify-content: space-between;
    list-style: none;
    margin: 0;
    padding: 0
}
.navbar ul li + li{margin-left: 2rem}
.navbar ul li a{
    text-decoration: none;
    color: var(--dark-clr);
    font-size: 1.125rem;
    transition: .1s ease-out;
    text-transform: uppercase;
    cursor: pointer
}
.navbar ul li a.active,
.navbar ul li a:hover,
.navbar ul li a:focus {
    color: var(--primary-clr);
    transition: .1s ease-out;
    position: relative
}
.navbar ul li a.active::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 4px;
    left: 0;
    bottom: -25px;
    background-color: var(--primary-clr)
}
.shopping-cart {
    font-size: 1.125rem;
    color: white;
    background: var(--primary-clr) ;
    padding: .35em 1em;
    border-radius: 100px;
    max-height: 30px;
    margin-left: 2em;
    cursor: pointer;
    transition: .15s ease-out
}
.shopping-cart:hover {
    background-color: var(--primary-darken-clr);
    transition: .15s ease-out
}
.icon {
    font-size: 2.5rem;
    color: var(--dark-clr);
    cursor: pointer;
    margin-left: 1rem;
    border-radius: 35%;
    padding: 5px;
    transition: .1s ease-out;
    display: none
}
.icon:hover {
    background-color: var(--primary-soft-clr);
    transition: .1s ease-out
}

@media only screen and (max-width: 850px) {
    #container.header {-webkit-flex-basis: 85%;flex-basis: 85%}
    .icon {display: block}
    .navbar ul {display: none}
    .navbar #hidden li a.active::before {display: none}
    .navbar #hidden {
        display: block;
        position: absolute;
        left: 0;
        top: 72px;
        text-align: center;
        width: 100%
    }
    .navbar #hidden li {
        padding: 1em 0;
        background-color: var(--primary-clr)
    }
    .navbar #hidden li + li {margin: 0}
    .navbar #hidden li a.active,
    .navbar #hidden li:hover a,
    .navbar #hidden li:focus a,
    .navbar #hidden li a:hover,
    .navbar #hidden li a:focus {color: white}
}
.btn {
    font-size: 0.875rem;
    padding: .6em 1em;
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: 1px;
    cursor: pointer;
    transition: .1s ease-out;
}
.btn:hover,
.btn:focus {transition: .1s ease-out}
.split-vertically{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding-bottom: 1.8em;
    border-radius: 10px;
    transition: .5s ease-in;
    background: #fff;
    box-shadow:inset 0px 0px 0px 1px var(--light-clr);
}
.flex-col-vertically img {
    width: 100%;
    max-width: 18rem;
    border-radius: 8px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
}
.typo h3 {
    font-size: 1.625rem;
    margin: .6em 0;    
}
.typo p, .typo h3 {
    max-width: 25ch;
}
.typo p {
    margin: 1em 0 1.4em;
}
.typo {
    padding: 0 1em 0 1em;
    -webkit-flex-basis: 100%;
            flex-basis: 100%;
    max-width: 18rem;
}
.split-vertically:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    transition: all .35s ease-in-out;
    background: var(--secondary-soft-clr)
}
.heading {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin: 0 0 1.8em;
    padding: 0 6em
}
.heading h2 {
    font-size: 1.625rem;
    margin: 0;
    text-transform: uppercase
}
#container.meals {
    border-radius: 15px;
    padding: 4em 0;
    box-shadow:inset 0px 0px 0px 1px var(--light-clr);
    background: white;
    min-height: 80%;
    height: -webkit-max-content;
    height: max-content;
}
.meals .split {padding: 0 6em}
.split-vertically + .split-vertically {margin-left: 1em}

@media only screen and (max-width: 1000px) {
    .meals .split {padding: 0 3em}
    .heading  {padding: 0 3em}
}
@media only screen and (max-width: 850px) {
    .meals .split {-webkit-flex-direction: column;flex-direction: column}
    .heading h2 {margin: 0 0 1em}
    .split-vertically + .split-vertically {margin: 1.5em 0 0}
    #container.meals {
        margin: 2em auto;
        padding: 3em 0
    }
    .heading  {
        display: block;
        text-align: center
    }
}
.hero {
    min-height: 1000px;
    width: 100%;
    max-height: 1500px;
    position: relative
}
.hero__landing-page {
    height: calc(100vh - 72px);
    display: -webkit-flex;
    display: flex;
    max-height: 700px
}
.hero__meals{
    min-height: 625px;
    max-height: 850px
}
.split {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    -webkit-align-self: center;
            align-self: center
}
.flex-col {-webkit-flex-basis: 100%;flex-basis: 100%}
.flex-col img {float: right}
.flex-col .hero__description {
    line-height: 1.4;
    font-size: 1.125rem;
    max-width: 45ch;
    opacity: .8;
    margin: 1em 0 1.5em
}
.flex-col .hero__heading {
    font-size: 3.125rem;
    margin: 0;
    position: relative
}
.flex-col .hero__heading::before {
    content: '';
    position: absolute;
    left: 70px;
    bottom: 0;
    width: 170px;
    height: 6.5px;
    background-color: var(--primary-clr);
    border-radius: 100px;
    -webkit-transform-origin: left;
            transform-origin: left;
    -webkit-animation: line ease-out infinite 4s;
            animation: line ease-out infinite 4s;
    cursor: text
}
.bg {
    position: absolute;
    height: 100%;
    top: 0;
    right: 0;
    z-index: -99
}

@-webkit-keyframes line {
    0% {-webkit-transform: scaleX(0);transform: scaleX(0)}
    50% {
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
        width: 0;
        left: 190px;
        background-color: var(--secondary-clr)
    }
    100% {-webkit-transform: scaleX(0);transform: scaleX(0)}
}

@keyframes line {
    0% {-webkit-transform: scaleX(0);transform: scaleX(0)}
    50% {
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
        width: 0;
        left: 190px;
        background-color: var(--secondary-clr)
    }
    100% {-webkit-transform: scaleX(0);transform: scaleX(0)}
}

@media only screen and (max-width: 950px) {
    .hero__landing-page .split {-webkit-flex-direction: column-reverse;flex-direction: column-reverse}
    .hero__landing-page {
        height: unset;
        padding: 2em 0;
        max-height: unset;
    }
    .flex-col img {
        width: 13rem;
        margin-bottom: 1em
    }
    .hero__meals{max-height: unset}
    .hero {max-height: unset}
    .bg {display: none}
}
.drink-img img {
    max-width: 10rem;
    min-width: 10rem;
    border-radius: 10px;
    width: 100%;
    transition: .15s ease-out
}
.drink-item h3 {
    font-size: 1.625rem;
    color: white;
    margin-bottom: 0
}
.drink-item {
    text-align: center;
    cursor: pointer
}
.drink-item:hover img,
.drink-item:focus img {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
    transition: .15s ease-out
}
@media only screen and (max-width: 850px) {
    .drink-item:hover img,
    .drink-item:focus img {-webkit-transform: none;transform: none} 
}
@media only screen and (max-width: 550px) {
    .drink-img img {
        min-width: 11.5rem;
        border-radius: none
    }
}
.drinks,
.slideshow,
.slideshow span,
.slideshow-items {display: -webkit-flex;display: flex}

.drinks {
    padding: 4em 0;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center
}
.drinks-section {
    width: 100%;
    background-color: var(--primary-clr);
    margin-top: 4em
}
.drinks .heading h2 {color: white}
.slideshow {
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center
}
.slideshow-items {
    overflow-y: auto;
    max-width: 750px
}
.slideshow-items::-webkit-scrollbar {display: none}
.drink-item + .drink-item {margin-left: 2em}
.slideshow span {
    padding: .5em;
    background-color: #000;
    border-radius: 50%;
    width: 52px;
    height: 52px;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    cursor: pointer;
    opacity: .3;
    transition: .1s ease-out
}
.slideshow span:hover,
.slideshow span:focus {
    opacity: .5;
    transition: .1s ease-out
}
.arrow-icon {
    font-size: 2em;
    color: var(--primary-clr)
}

@media only screen and (max-width: 850px) {
    .slideshow-items {max-width:320px}
}
@media only screen and (max-width: 550px) {
    .slideshow-items {max-width: 200px}
    .arrow-icon {font-size: 1.5em}
    .slideshow span {
        width: 40px;
        height: 40px 
    }
}



.contact-section {
    position: relative;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
    padding: 6em 0;
    background: var(--secondary-clr)
}
.contact {
    background-color: white;
    padding: 2em;
    border-radius: 15px;
    max-width: 450px !important;
    transition: .15s ease-out
}
.contact h1 {font-size: 1.625rem}
.contact__form {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column
}
.contact__form > * {
    padding: 1em;
    border-radius: 5px;
    border: solid 1px var(--light-clr)
}
.contact__form input {margin-bottom: 1em}
.contact__form textarea {
    margin-bottom: 2em;
    resize: none;
    font-family: 'Rubik'
}
.contact__form > *:focus {border: solid 1px var(--secondary-clr)}

.particle {
    position: absolute;
    width: 50px;
}
.plus {
    top: 10rem;
    right: 10rem;
    -webkit-animation: rotate 6s ease-out infinite;
            animation: rotate 6s ease-out infinite;
}
.bubbles {
    width: 100px;
    bottom: 10rem;
    left: 10rem;
    -webkit-animation: slide 20s ease-out infinite;
            animation: slide 20s ease-out infinite;

}

@-webkit-keyframes rotate {
    0% {
        right: 11rem;
        top: 13rem;
        -webkit-transform: rotateZ(360deg);
                transform: rotateZ(360deg);
    }
    30% {
        right: 10rem;
        -webkit-transform: rotateZ(0deg);
                transform: rotateZ(0deg);
    }
    100% {
        right: 11rem;
        top: 13rem;
        -webkit-transform: rotateZ(-360deg);
                transform: rotateZ(-360deg);
    }
}

@keyframes rotate {
    0% {
        right: 11rem;
        top: 13rem;
        -webkit-transform: rotateZ(360deg);
                transform: rotateZ(360deg);
    }
    30% {
        right: 10rem;
        -webkit-transform: rotateZ(0deg);
                transform: rotateZ(0deg);
    }
    100% {
        right: 11rem;
        top: 13rem;
        -webkit-transform: rotateZ(-360deg);
                transform: rotateZ(-360deg);
    }
}
@-webkit-keyframes slide {
    0% {
        bottom: 10rem;
        left: 10rem;
        -webkit-transform: rotateZ(360deg);
                transform: rotateZ(360deg);
    }
    50% {
        bottom: 13rem;
        left: 12rem;
        -webkit-transform: rotateZ(0deg);
                transform: rotateZ(0deg);
    }
    100% {
        bottom: 10rem;
        left: 10rem;
        -webkit-transform: rotateZ(-360deg);
                transform: rotateZ(-360deg);
    }
}
@keyframes slide {
    0% {
        bottom: 10rem;
        left: 10rem;
        -webkit-transform: rotateZ(360deg);
                transform: rotateZ(360deg);
    }
    50% {
        bottom: 13rem;
        left: 12rem;
        -webkit-transform: rotateZ(0deg);
                transform: rotateZ(0deg);
    }
    100% {
        bottom: 10rem;
        left: 10rem;
        -webkit-transform: rotateZ(-360deg);
                transform: rotateZ(-360deg);
    }
}

@media only screen and (max-width:850px) {
    .particles {display: none}
}

